@import url("https://fonts.googleapis.com/css2?family=Lobster&family=Kalam&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

------------root-color---------- :root {
  --primary: #176cff;

  --success: #198754;
  --danger: #dc3545;
  --warning: #c8990c;
  --info: #f8f8f8;
  --light: #f8f9fa;
  --dark: #343a40;
  --black: #000000;
  --white: #ffffff;
  --secondary: #10195d;
}

html,
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  scroll-behavior: smooth;
  font-family: "Kalam", cursive;
  font-family: "Lobster", cursive;
  font-family: "Poppins";
}

a:hover {
  text-decoration: none;
}

h1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  color: #000000;
}

h2 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  color: #000000;
}

h3 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  color: #000000;
}

h4 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  color: #000000;
}

h5 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  color: #000000;
}

h6 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  color: #000000;
}

p {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
}

::ng-deep::-webkit-scrollbar {
  width: 0.1px;
  scroll-behavior: smooth !important;
}

/* Handle */
::ng-deep::-webkit-scrollbar-thumb {
  background: #cccccc;
  border-radius: 10px;
}
::ng-deep.getStartedBtn {
  background: #176cff;
  border-radius: 30px;
  width: 170px;
  height: 47px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 42px;
  gap: 10px;
}
